import React, {useRef} from 'react';
import {Dialog} from "../Dialog";

export default function TButtonModal(props) {
    const {open, setOpen, messageText, acceptText, cancelText, onAccept, onCancel, title} = props;
    const ref = useRef(1);
    //logo
    const handleClose = () => {
        setOpen(false);
    }
    const handleOpen = () => {
        setOpen(true);
    }
    return (
        <Dialog dialogTitle={title} open={open} handleOpen={handleOpen} handleClose={handleClose}>

            <div className="grid grid-flow-row m-auto text-center w-full h-full p-4" ref={ref}>
                {messageText}
            </div>
            <div className="flex justify-between w-full p-8">
                <button className="" onClick={onAccept}>{acceptText}</button>
                <button className="" onClick={onCancel}>{cancelText}</button>
            </div>

        </Dialog>
    );
}